<template>
  <div id="pageStyleOne">
    <div class="leftBox" ref="leftBox">
      <slot name="slot1"></slot>
    </div>
    <div class="rightBox" v-if="rightBoxShow">
      <slot name="slot2"></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: "PageStyleOne",
  components: {},
  data() {
    return {
      rightBoxShow: true,
    };
  },
  mounted() {
    this.windowSizeFun();
  },
  methods: {
    windowSizeFun() {
      if (this.$store.state.windowSize > 1000) {
        this.rightBoxShow = true;
        this.$refs.leftBox.style.width = "75%";
      } else {
        this.rightBoxShow = false;
        this.$refs.leftBox.style.width = "100%";
      }
    },
  },
  watch: {
    "$store.state.windowSize"() {
      this.windowSizeFun();
    },
  },
};
</script>

<style scoped>
#pageStyleOne {
  width: 100%;
  display: flex;
}
.leftBox {
  width: 75%;
}
.rightBox {
  width: 25%;
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>
