<template>
<!-- 不应该在这层写外部样式 -->
  <div id="TextStyleOne" ref="TextStyleOne">
    <div class="box1" v-if="newText == true"></div>
    <div class="textBigBox" ref="textBigBox">
      <div class="tittleBox">
        <div class="text1Box">
          <slot name="text1"></slot>
        </div>
        <div class="text2Box">
          <slot name="text2"></slot>
        </div>
      </div>
      <div class="text3Box" ref="text3Box">
        <slot name="text3"></slot>
      </div>
      <div class="text4Box" ref="text4Box">
        <slot name="text4"></slot>
      </div>
      <div class="text5Box">
        <slot name="text5"></slot>
      </div>
    </div>
    <div class="bigBox2" v-if="(index + 1) == length">
      <div class="box2"></div>
    </div>
  </div>
</template>

<script>

export default {
  name:'TextStyleOne',
  components: {

  },
  props:{
    index:{
      type:Number,
      default:0
    },
    length:{
      type:Number,
      default:1
    },
    jsonLength:{
      type:Number,
      default:0
    },
    newText:{
      type:Boolean,
      default:false
    }
  },
  data() {
    return {
      
    }
  },
  mounted(){
    this.styleOneFun()
    this.styleTwoFun()
    this.styleThreeFun()
  },
  methods:{
    styleOneFun(){
      if((this.index + 1) < this.length){
        this.$refs.TextStyleOne.style.paddingBottom = "0px"
      }
    },
    styleTwoFun(){
      if((this.index + 1) !== 1){
        this.$refs.TextStyleOne.style.paddingTop = "0px"
      }
    },
    styleThreeFun(){
      if(this.jsonLength < 3){
        this.$refs.text3Box.style.marginBottom = "0px"
        this.$refs.text4Box.style.marginBottom = "0px"
        this.$refs.textBigBox.style.padding = "20px 40px 0px 40px"
      }
    }
    
  },
}
</script>

<style scoped>
#TextStyleOne{
  width: calc(100% - 60px);
  padding: 30px;
}
.box1{
  width: 80px;
  height: 25px;
  background: rgb(192, 202, 255);
}
.textBigBox{
  width: calc(100% - 80px);
  padding: 20px 40px;
}
.tittleBox{
  width: 100%;
  position: relative;
}
.tittleBox .text1Box{
  color: rgb(192, 202, 255);
}
.tittleBox .text2Box{
  position: absolute;
  bottom: 0;
  right: 0;
  color: rgb(192, 202, 255);
}

.text3Box{
  margin-bottom: 15px;
  color: rgb(192, 202, 255);
}
.text4Box{
  margin-bottom: 15px;
}
.text5Box{
  width: calc(100% - 30px);
  padding-left: 30px;
}

.bigBox2{
  height: 50px;
  display: flex;
  width: 100%;
  position: relative;
}
.box2{
  position: absolute;
  top: 0;
  right: -30px;
  width: 80px;
  height: 80px;
  background: rgb(192, 202, 255);
}
</style>
